import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import contactStyle from "./style/contact.module.css";

const Home = () => {
  useEffect(() => {
    // AOS 초기화
    AOS.init({
      duration: 1200, // 애니메이션 지속 시간
      easing: "ease-in-out", // 애니메이션 타이밍 함수
      once: true, // 스크롤할 때 한번만 애니메이션이 실행되도록
      mirror: true, // 애니메이션이 실행되기 전에 다시 실행되도록
    });
  }, []);

  return (
    <div className={contactStyle.contactContainer}>
      <div className={contactStyle.contactContentBox}>
        <div className={contactStyle.contactTitleBox}>Contact</div>
        <div>email | sls789456@naver.com</div>
        <div>010 - 5119 - 6910</div>
        <div>GitHub | https://github.com/kyuchory</div>
      </div>
    </div>
  );
};

export default Home;
