import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import homeStyle from "./style/home.module.css";


const Home = () => {
  //830px
  return (
    <section
      id="home"
      className={homeStyle.homeContainer}
      style={{ backgroundColor: "#F0F0F0", marginTop: "70px" }}
    >
      <div className={homeStyle.homeContentBox}>
        <div className={homeStyle.homeTitleBox}>
          <div>Portfolio of front-end developer </div>
          <div>신규철</div>
        </div>
        <div className={homeStyle.homeTextBox}>
          <div>안녕하세요.</div>
          <div>프론트엔드 개발자를 지망하는 신규철이라고 합니다.</div>
          <div>팀원과의 원활한 소통을 최우선으로 여기며,</div>
          <div>새로운 기술과 변화를 유연하게 받아들이고자 노력합니다.</div>
        </div>
      </div>
    </section>
  );
};

export default Home;
