import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import aboutStyle from "./style/aboutme.module.css";

import profileImg from "../img/profile.png";
import birthImg from "../img/birth.png";
import schoolImg from "../img/school.png";
import emailImg from "../img/email.png";
import phoneImg from "../img/phone.png";
import certificateImg from "../img/certificate.png";

const AboutMe = () => {
  useEffect(() => {
    // AOS 초기화
    AOS.init({
      duration: 1200, // 애니메이션 지속 시간
      easing: "ease-in-out", // 애니메이션 타이밍 함수
      once: true, // 스크롤할 때 한번만 애니메이션이 실행되도록
      mirror: true, // 애니메이션이 실행되기 전에 다시 실행되도록
    });
  }, []);

  return (
    <section
      id="about"
      className={aboutStyle.aboutContainer}
      data-aos="fade-up"
    >
      <div className={aboutStyle.aboutContentBox}>
        <div className={aboutStyle.aboutTitleBox}>
          <div>About Me</div>
        </div>
        <div className={aboutStyle.aboutDetailBox}>
          <div className={aboutStyle.aboutDetail}>
            <div className={aboutStyle.aboutDetailImgBox}>
              <img src={profileImg} alt="profile image" />
            </div>
            <div className={aboutStyle.aboutDetailContentBox}>
              <div className={aboutStyle.aboutDetailTitle}>
                <div>이름</div>
              </div>
              <div className={aboutStyle.aboutDetailContent}>
                <div>신규철</div>
              </div>
            </div>
          </div>
          <div className={aboutStyle.aboutDetail}>
            <div className={aboutStyle.aboutDetailImgBox}>
              <img src={birthImg} alt="birth image" />
            </div>
            <div className={aboutStyle.aboutDetailContentBox}>
              <div className={aboutStyle.aboutDetailTitle}>
                <div>생년 월일</div>
              </div>
              <div className={aboutStyle.aboutDetailContent}>
                <div>1999.07.19</div>
              </div>
            </div>
          </div>
          <div className={aboutStyle.aboutDetail}>
            <div className={aboutStyle.aboutDetailImgBox}>
              <img src={schoolImg} alt="school image" />
            </div>
            <div className={aboutStyle.aboutDetailContentBox}>
              <div className={aboutStyle.aboutDetailTitle}>
                <div>학력</div>
              </div>
              <div className={aboutStyle.aboutDetailContent}>
                <div>단국대 컴퓨터공학과</div>
              </div>
            </div>
          </div>
          <div className={aboutStyle.aboutDetail}>
            <div className={aboutStyle.aboutDetailImgBox}>
              <img src={emailImg} alt="email image" />
            </div>
            <div className={aboutStyle.aboutDetailContentBox}>
              <div className={aboutStyle.aboutDetailTitle}>
                <div>이메일</div>
              </div>
              <div className={aboutStyle.aboutDetailContent}>
                <div>sls789456@naver.com</div>
              </div>
            </div>
          </div>
          <div className={aboutStyle.aboutDetail}>
            <div className={aboutStyle.aboutDetailImgBox}>
              <img src={phoneImg} alt="phone image" />
            </div>
            <div className={aboutStyle.aboutDetailContentBox}>
              <div className={aboutStyle.aboutDetailTitle}>
                <div>연락처</div>
              </div>
              <div className={aboutStyle.aboutDetailContent}>
                <div>010-5119-6910</div>
              </div>
            </div>
          </div>
          <div className={aboutStyle.aboutDetail}>
            <div className={aboutStyle.aboutDetailImgBox}>
              <img src={certificateImg} alt="certificate image" />
            </div>
            <div className={aboutStyle.aboutDetailContentBox}>
              <div className={aboutStyle.aboutDetailTitle}>
                <div>자격증</div>
              </div>
              <div className={aboutStyle.aboutDetailContent}>
                <div>정보 처리 기사</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default AboutMe;
