import React, { useEffect, useRef, useState } from "react";
import "./App.css";
import AOS from "aos";
import "aos/dist/aos.css";
import Home from "./components/Home";
import Header from "./components/Header";
import AboutMe from "./components/AboutMe";
import Skills from "./components/Skills";
import Projects from "./components/Projects";
import Contact from "./components/Contact";

const App = () => {
  const [activeSection, setActiveSection] = useState(); // 기본 활성화 섹션

  const scrollRef = useRef([]);

  const handleScrollView = (target) => {
    const name = target;

    console.log("클릭됨", name);
    const category = {
      Home: 0,
      AboutMe: 1,
      Skills: 2,
      Projects: 3,
    };
    scrollRef.current[category[name]].scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    // AOS 초기화
    AOS.init({
      duration: 1200, // 애니메이션 지속 시간
      easing: "ease-in-out", // 애니메이션 타이밍 함수
      once: true, // 스크롤할 때 한번만 애니메이션이 실행되도록
      mirror: true, // 애니메이션이 실행되기 전에 다시 실행되도록
    });

    // IntersectionObserver 설정
    const options = {
      root: null, // 뷰포트
      rootMargin: "0px",
      threshold: 0.5, // 섹션이 50% 보이면 활성화
    };

    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          setActiveSection(entry.target.id);
        }
      });
    }, options);

    // 섹션들을 관찰
    const sections = document.querySelectorAll(".section");
    sections.forEach((section) => observer.observe(section));

    return () => {
      sections.forEach((section) => observer.unobserve(section));
    };
  }, []);

  return (
    <div className="App">
      <Header handleScrollView={handleScrollView} />
      <div
        ref={(el) => {
          scrollRef.current[0] = el;
        }}
      >
        <Home />
      </div>
      <div
        ref={(el) => {
          scrollRef.current[1] = el;
        }}
      >
        <AboutMe />
      </div>
      <div
        ref={(el) => {
          scrollRef.current[2] = el;
        }}
      >
        <Skills />
      </div>
      <div
        ref={(el) => {
          scrollRef.current[3] = el;
        }}
      >
        <Projects />
      </div>
      <Contact />
    </div>
  );
};

export default App;
